var durations = {};
var now;
export var reset = function reset() {
  return durations = {};
};
export var enter = function enter(id) {
  now = performance.now();
};
export var exit = function exit(id) {
  durations[id] = durations[id] || 0;
  durations[id] += performance.now() - now;
};
export var log = function log() {
  return console.log(durations);
};